
* {
  margin: 0;
  padding: 0;
}
ul{
  list-style: none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* color: #2c3e50; */
  display: -webkit-box;
  display: flex;
  display: -ms-flexbox;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
}
.content{
  width: 100%;
  /* height: 100%; */
  background-color:  #f4f4f4;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 0.6rem;
}
.bottom-msg{
  /* position: absolute; */
  /* bottom: 0; */
  height: 6vh;
  width: 100%;
  background-color:  #f4f4f4;
  text-align: center;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-flex: wrap;
      -ms-flex-positive: wrap;
          flex-grow: wrap;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  font-size: 0.6rem;
}
.bottom-msg .bottom-content{
  line-height: 3vh;
  vertical-align: middle;
}

input {
    background: none;
    outline: none;
    border: none;
}
.header{
  width: 1200px;
  height: 10vh;
  background-color:  #f4f4f4;
  display: inline-block;
  margin: 0 auto;
  overflow: hidden;
  font-size:0.7rem;
}
.header li {
    vertical-align:middle;/* 此样式应放在子标签上，放在父标签上无效！ */
}
.home,.user,.setting{
  height: 100%;
  display: inline-block;
  line-height:10vh;
}
.headerIcon {
    height: 10vh;
    padding: 1vh;
    box-sizing: border-box; /* 默认 content-box 导致padding(border也是)计算在height之外 换成border-box则将padding计算在内 */
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box; /* Safari */
}
.header img{
    height: 100%;
    width: 10vh;
}
.header .search-content{
    border: none;
    border-radius: 50px;
    border: 2px solid #fff;
    /* vertical-align: middle; */
    margin: auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    line-height: 6vh;
    display: inline-block;
}
.header .search{
    border-radius: 50px;
}
@-webkit-keyframes hover-zoom{
0%{
    background: #f4f4f4;
}
50%{
    background: #f4f4f4
}
100%{
    background: coral;
}
}
.hover :hover{
    -webkit-animation: hover-zoom .8s;
            animation: hover-zoom .8s;
    background-color: coral;
}
/* 问题情况是：设置li hover 样式，结果出现hover时，只有里面的文字发生变化；
   问题原因是：a:hover 的权重大于li:hover,并且a标签是行内元素；
   改进措施是：将li 中的a 设置为 display：block；
 */
li a {
  display: block;
}
.right{
  float: right;
  position: absolute;
}
.right ::after{
  clear: both;
}
.position1{
    right: 2%;
}
.position2{
    right: 7%;
}
.position3{
    right: 15%;
}

h1[data-v-32dd4e98], h2[data-v-32dd4e98] {
  font-weight: normal;
}
ul[data-v-32dd4e98] {
  list-style-type: none;
  padding: 0;
}
li[data-v-32dd4e98] {
  display: inline-block;
  margin: 0 10px;
}
a[data-v-32dd4e98] {
  color: #42b983;
}

